'use client'

import { useState, useEffect } from 'react'
import { useAuth } from '@/lib/auth/AuthContext'
import {
  Users,
  DollarSign,
  MessageSquare,
  BarChart3,
  TrendingUp,
  TrendingDown,
  AlertTriangle,
  CheckCircle,
  Activity,
  Server,
} from 'lucide-react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { LoadingSpinner } from '@/components/ui/LoadingSpinner'

interface MetricCard {
  title: string
  value: string
  change: string
  changeType: 'increase' | 'decrease' | 'neutral'
  icon: React.ComponentType<any>
}

interface SystemAlert {
  id: string
  type: 'warning' | 'error' | 'info'
  title: string
  message: string
  timestamp: string
}

const metrics: MetricCard[] = [
  {
    title: 'Active Agents',
    value: '12',
    change: '+2 this month',
    changeType: 'increase',
    icon: Users
  },
  {
    title: 'Monthly Costs',
    value: '$2,847',
    change: '+12% from last month',
    changeType: 'increase',
    icon: DollarSign
  },
  {
    title: 'Total Conversations',
    value: '1,234',
    change: '+18% this week',
    changeType: 'increase',
    icon: MessageSquare
  },
  {
    title: 'Conversion Rate',
    value: '24.5%',
    change: '+3.2% this month',
    changeType: 'increase',
    icon: BarChart3
  }
]

const systemAlerts: SystemAlert[] = [
  {
    id: '1',
    type: 'warning',
    title: 'High API Usage',
    message: 'WhatsApp API usage is approaching monthly limit (85% used)',
    timestamp: '5 minutes ago'
  },
  {
    id: '2',
    type: 'info',
    title: 'Scheduled Maintenance',
    message: 'System maintenance scheduled for tonight at 2:00 AM SGT',
    timestamp: '1 hour ago'
  }
]

const recentAgents = [
  { id: '1', name: 'Sarah Chen', email: 'sarah.chen@propertyhub.sg', status: 'active', joinedAt: '2 days ago' },
  { id: '2', name: 'Michael Wong', email: 'michael.wong@propertyhub.sg', status: 'pending', joinedAt: '1 week ago' },
  { id: '3', name: 'Lisa Tan', email: 'lisa.tan@propertyhub.sg', status: 'active', joinedAt: '2 weeks ago' },
]

export default function AdminDashboard() {
  const [loading, setLoading] = useState(true)
  const { user, hasPermission } = useAuth()

  useEffect(() => {
    // Simulate loading
    const timer = setTimeout(() => setLoading(false), 1000)
    return () => clearTimeout(timer)
  }, [])

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  // Check admin permission
  if (!hasPermission('manage_system')) {
    return (
      <div className="flex items-center justify-center h-64">
        <Card>
          <CardContent className="p-6 text-center">
            <h2 className="text-lg font-semibold mb-2">Access Denied</h2>
            <p className="text-muted-foreground">You don't have permission to access this page.</p>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-semibold">Admin Dashboard</h1>
          <p className="text-muted-foreground mt-1">
            Monitor and manage your Outpaced system
          </p>
        </div>
        <div className="flex space-x-3">
          <Button variant="outline">
            Export Report
          </Button>
          <Button>
            Add New Agent
          </Button>
        </div>
      </div>

      {/* System Alerts */}
      {systemAlerts.length > 0 && (
        <div className="space-y-3">
          {systemAlerts.map((alert) => (
            <Card
              key={alert.id}
              className={`border-l-4 ${
                alert.type === 'warning'
                  ? 'border-l-yellow-400 bg-yellow-50/50'
                  : alert.type === 'error'
                  ? 'border-l-red-400 bg-red-50/50'
                  : 'border-l-blue-400 bg-blue-50/50'
              }`}
            >
              <CardContent className="p-4">
                <div className="flex items-start">
                  <div className="flex-shrink-0">
                    {alert.type === 'warning' ? (
                      <AlertTriangle className="h-5 w-5 text-yellow-600" />
                    ) : (
                      <CheckCircle className="h-5 w-5 text-blue-600" />
                    )}
                  </div>
                  <div className="ml-3 flex-1">
                    <h3 className="text-sm font-medium">
                      {alert.title}
                    </h3>
                    <p className="text-sm text-muted-foreground mt-1">
                      {alert.message}
                    </p>
                    <p className="text-xs text-muted-foreground mt-2">
                      {alert.timestamp}
                    </p>
                  </div>
                  <Button variant="ghost" size="sm" className="ml-3">
                    Ã—
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}

      {/* Metrics Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {metrics.map((metric) => {
          const Icon = metric.icon
          const TrendIcon = metric.changeType === 'increase' ? TrendingUp :
                          metric.changeType === 'decrease' ? TrendingDown : null
          return (
            <Card key={metric.title}>
              <CardContent className="p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm font-medium text-muted-foreground">
                      {metric.title}
                    </p>
                    <p className="text-2xl font-semibold mt-2">
                      {metric.value}
                    </p>
                    <div className="flex items-center mt-2">
                      {TrendIcon && (
                        <TrendIcon className={`h-4 w-4 mr-1 ${
                          metric.changeType === 'increase' ? 'text-green-600' : 'text-red-600'
                        }`} />
                      )}
                      <span className={`text-sm ${
                        metric.changeType === 'increase'
                          ? 'text-green-600'
                          : metric.changeType === 'decrease'
                          ? 'text-red-600'
                          : 'text-muted-foreground'
                      }`}>
                        {metric.change}
                      </span>
                    </div>
                  </div>
                  <div className="h-12 w-12 rounded-lg bg-primary/10 flex items-center justify-center">
                    <Icon className="h-6 w-6 text-primary" />
                  </div>
                </div>
              </CardContent>
            </Card>
          )
        })}
      </div>

      {/* Recent Activity Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Recent Agents */}
        <Card>
          <CardHeader>
            <div className="flex items-center justify-between">
              <CardTitle>Recent Agents</CardTitle>
              <Button variant="outline" size="sm">
                View All
              </Button>
            </div>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {recentAgents.map((agent) => (
                <div key={agent.id} className="flex items-center justify-between">
                  <div className="flex items-center space-x-3">
                    <div className="h-8 w-8 bg-primary/10 rounded-full flex items-center justify-center">
                      <span className="text-sm font-medium text-primary">
                        {agent.name.charAt(0)}
                      </span>
                    </div>
                    <div>
                      <p className="text-sm font-medium">
                        {agent.name}
                      </p>
                      <p className="text-xs text-muted-foreground">
                        {agent.email}
                      </p>
                    </div>
                  </div>
                  <div className="text-right">
                    <Badge variant={agent.status === 'active' ? 'secondary' : 'outline'}>
                      {agent.status}
                    </Badge>
                    <p className="text-xs text-muted-foreground mt-1">
                      {agent.joinedAt}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        {/* System Performance */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Activity className="h-5 w-5" />
              System Performance
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <span className="text-sm text-muted-foreground">API Response Time</span>
                <Badge variant="secondary" className="text-green-600">142ms</Badge>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm text-muted-foreground">Database Performance</span>
                <Badge variant="secondary" className="text-green-600">Optimal</Badge>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm text-muted-foreground">WhatsApp API Status</span>
                <Badge variant="secondary" className="text-green-600">Connected</Badge>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm text-muted-foreground">Server Uptime</span>
                <span className="text-sm font-medium">99.9%</span>
              </div>
              <Progress value={99.9} className="mt-2" />
              <div className="mt-4 pt-4 border-t">
                <Button variant="outline" size="sm" className="w-full">
                  <Server className="h-4 w-4 mr-2" />
                  View Detailed Metrics
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
